// Module
___CSS_LOADER_EXPORT___.push([module.id, `.report-container {
    padding: 30px;
    max-width: 800px;
    margin: 20px auto;
    background-color: #28293d;
    border-radius: 10px;
    color: white;
  }
  
  .report-header {
    margin-bottom: 30px;
    text-align: center;
  }
  
  .report-header h1 {
    font-size: 28px;
    color: #f8f8ff;
  }
  
  .report-section {
    margin-bottom: 30px;
  }
  
  .report-section h2 {
    font-size: 22px;
    color: #f8f8ff;
    margin-bottom: 15px;
  }
  
  .report-section button,
  .report-section input[type="file"] {
    padding: 12px 20px;
    font-size: 16px;
    background-color: #0066cc;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    width: 100%; /* 버튼을 너비 100%로 설정 */
    margin-bottom: 10px;
  }
  
  .report-section button:hover,
  .report-section input[type="file"]:hover {
    background-color: #004c99;
  }
  
  .report-section input[type="text"] {
    width: 100%;
    padding: 15px;
    font-size: 16px;
    border: 2px solid #444;
    border-radius: 5px;
    background-color: #3b3b5a;
    color: white;
    margin-bottom: 15px;
  }
  
  .report-section input[type="text"]::placeholder {
    color: #cccccc;
  }
  
  .location-status {
    margin-top: 10px;
    font-size: 16px;
    color: #ff6666;
  }
  
  .report-submit {
    display: flex;
    justify-content: center;
  }
  
  .report-submit button {
    padding: 15px 30px;
    font-size: 18px;
    background-color: #0066cc;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    width: 100%; /* 신고하기 버튼을 너비 100%로 설정 */
  }
  
  .report-submit button:hover {
    background-color: #004c99;
  }
